import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { Helmet } from '../App'; // Import our custom SEO component 
import Navbar from './Navbar';
import Footer from './Footer';
import blogs from '../data/blogs'; // Import the blogs list
import BlogList from './BlogList';

const BlogPage = () => {
  const { link } = useParams();
  const blog = blogs.find((b) => b.link === link);
  const [ContentComponent, setContentComponent] = useState(null);

  useEffect(() => {
    if (blog) {
      import(`${blog.contentComponent}`).then((module) => {
        setContentComponent(() => module.default);
      }).catch((error) => {
        console.error("Error loading content component:", error);
      });
    }
  }, [blog]);

  if (!blog) {
    return <div>Blog not found</div>;
  }

  // Filter out the current blog from the list of blogs
  const otherBlogs = blogs.filter((b) => b.link !== link);

  // Format the date for display and schema
  const formattedDate = blog.uploadDate ? new Date(blog.uploadDate).toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  }) : '';

  // Generate schema for the blog post
  const blogSchema = {
    "@context": "https://schema.org",
    "@type": "BlogPosting",
    "headline": blog.title,
    "description": blog.description,
    "image": `https://todayeggrates.com${blog.image}`,
    "datePublished": blog.uploadDate,
    "dateModified": blog.modifiedDate || blog.uploadDate,
    "author": {
      "@type": "Organization",
      "name": "TodayEggRates.com"
    },
    "publisher": {
      "@type": "Organization",
      "name": "TodayEggRates.com",
      "logo": {
        "@type": "ImageObject",
        "url": "https://todayeggrates.com/Favicon.ico"
      }
    },
    "mainEntityOfPage": {
      "@type": "WebPage",
      "@id": `https://todayeggrates.com/blog/${blog.link}`
    }
  };

  // Generate proper meta title and description
  const pageTitle = `${blog.title} - Today's Egg Rates | TodayEggRates.com`;
  const metaDescription = blog.metaDescription || blog.description;
  const canonicalUrl = `https://todayeggrates.com/blog/${blog.link}`;

  return (
    <div className="bg-gray-50 min-h-screen flex flex-col">
      <Helmet 
        title={pageTitle}
        description={metaDescription}
        canonicalUrl={canonicalUrl}
        schema={blogSchema}
      />
      <Navbar />
      <main className="flex-grow p-4 md:p-8 lg:p-12">
        <article className="max-w-4xl mx-auto p-6 bg-white rounded-lg shadow-md" itemScope itemType="https://schema.org/BlogPosting">
          <header className="mb-6">
            <h1 className="text-3xl font-bold mb-4 text-gray-800" itemProp="headline">{blog.title}</h1>
            <div className="flex items-center text-gray-600 mb-4">
              <time dateTime={blog.uploadDate} itemProp="datePublished">Published: {formattedDate}</time>
            </div>
            {blog.image && (
              <div className="mb-6">
                <img 
                  src={blog.image} 
                  alt={blog.title} 
                  className="w-full h-auto rounded-lg" 
                  itemProp="image"
                />
              </div>
            )}
          </header>
          <div className="prose max-w-none" itemProp="articleBody">
            {ContentComponent ? <ContentComponent /> : <p>Loading content...</p>}
          </div>
        </article>
        
        <section className="max-w-4xl mx-auto mt-12">
          <h2 className="text-2xl font-bold mb-6 text-gray-800">Related Articles</h2>
          <BlogList blogs={otherBlogs} />
        </section>
      </main>
      <Footer />
    </div>
  );
};

export default BlogPage;